body {
  margin: 0;
  height: 100%;
    background: #00bcd40a;
}
.slider__items {
  max-width: 300px;
  overflow: hidden;
  margin: 0 auto;
}

.slider__wrap {
  display: flex;
  justify-content: center;
  transform: translateX(1350px);
  transition: 2s ease-in;
}
.slider__item {
  margin: 0px 30px;
}
.slider__item:first-child {
  margin: 0px 30px 0px 60px;
}
.slider__item:last-child {
  margin: 0px 60px 0px 30px;
}
.slider__item {
  min-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.movie__header{
  color: #000;
    background: aliceblue;
    text-align: center;
    padding: 30px 20px;
    font-size: 38px;
    font-weight: 900;
}
.form{
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.input {
width: 80%;
background: url(../src/assets/search.png) no-repeat left center;
background-size: 10px;
background-position: 10px;
padding: 10px 0px 10px 30px;
border-radius: 18px 0px 0px 18px;
border-right: none;
}
.btn {
  padding: 10px 9px 10px 9px;
  border-radius: 0px 18px 18px 0px;
  border-left: none;
  background: transparent;
  cursor: pointer;
}
.btn-control__wrapper {
  display: flex;
  justify-content: space-evenly;
}
.btn-control {
cursor: pointer;
padding: 10px;
color: #000;
position: relative;
}
.left::before {
  content: "";
  position: absolute;
  width: 10px;
  left: 11px;
  transform: rotate(315deg);
  bottom: 16px;
  height: 1px;
  background-color: #000;
}
.left::after {
  content: "";
    position: absolute;
    width: 10px;
    height: 1px;
    transform: rotate(45deg);
    background-color: #000;
}
.right::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 1px;
  transform: rotate(315deg);
  background-color: #000;
}
.right::after {
  content: "";
  position: absolute;
  width: 10px;
  bottom: 16px;
  transform: rotate(45deg);
  height: 1px;
  background-color: #000;
}
.slider__item-title {
  font-size: 24px;
  color: #000;
  padding: 10px 0px;
  text-decoration: underline;
}
.slider__item-year {
  font-size: 24px;
}
.alert {
  text-align: center;
}
.reset {
  position: absolute;
    right: calc(10% + 20px);
    background: transparent;
    border: none;
}
.slider__item-img {
  border-radius: 10px;
}